import { useState, useEffect } from "react";

export default function IndexPage() {
  const [stats, useStats] = useState();
  useEffect(() => {
    async function fetchData() {
      const data = await fetch("").then(res => res.json());
      useStats(data);
    }
    console.log("fetching data");
    fetchData();
  }, []);

  return <p>Hello</p>;
}
